/*################################*\
    legacy  |  _c-KeynoteSpeaker.less
\*################################*/
.c-KeynoteSpeaker {
  align-items: center;
  margin-bottom: 1.5rem;
  margin-bottom: var(--lh);
  margin-bottom: 1lh;
}
@supports (margin-block-end: var(--v)) {
  .c-KeynoteSpeaker {
    margin-bottom: unset;
    margin-block-end: var(--lh);
    margin-block-end: 1lh;
  }
}
@media not all and (min-width: 30em) {
  .c-KeynoteSpeaker {
    flex-direction: column;
  }
}
.c-KeynoteSpeaker__Session,
.c-KeynoteSpeaker__Details {
  flex: 40%;
}
.c-KeynoteSpeaker__Session {
  justify-content: center;
}
@media not all and (min-width: 30em) {
  .c-KeynoteSpeaker__Session {
    margin-bottom: 1.5rem;
    margin-bottom: var(--lh);
    margin-bottom: 1lh;
  }
  @supports (margin-block-end: var(--v)) {
    .c-KeynoteSpeaker__Session {
      margin-bottom: unset;
      margin-block-end: var(--lh);
      margin-block-end: 1lh;
    }
  }
}
.c-KeynoteSpeaker__Headshot {
  flex: 20%;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-inline: 1rem;
  border-radius: 50%;
  overflow: hidden;
}
@media not all and (min-width: 30em) {
  .c-KeynoteSpeaker__Headshot {
    order: -1;
    margin-bottom: 1.5rem;
    margin-bottom: var(--lh);
    margin-bottom: 1lh;
  }
  @supports (margin-block-end: var(--v)) {
    .c-KeynoteSpeaker__Headshot {
      margin-bottom: unset;
      margin-block-end: var(--lh);
      margin-block-end: 1lh;
    }
  }
}
.c-KeynoteSpeaker__Headshot > img {
  width: 100%;
}
@media not all and (min-width: 30em) {
  .c-KeynoteSpeaker__Details {
    text-align: center;
  }
}
